/* All Styles For Front Facing Website 

#1 - Fonts
#2 - General Styles
#3 - Hero Image (Front Page)
#4 - Forms
#5 - Mobile Menu
#6 - Homepage Icons

*/
/* #1 - Fonts */
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
/* #2 - General Styles */
body {
  font-family: 'Montserrat', sans-serif; }

.valign-wrapper {
  align-items: center;
  display: flex;
  margin-top: 20px; }

.valign-wrapper .valign {
  display: block;
  margin: 0 auto; }

.content {
  padding-top: 80px; }

/* #3 - Hero Image */
.hero {
  background: url("/images/hero-image2.png");
  background-size: cover;
  display: table;
  height: 100vh;
  min-height: 100%;
  width: 100%; }
  .hero h1 {
    color: #fff;
    font-size: 7rem;
    margin-top: 10%;
    text-align: center; }
  .hero P {
    text-align: center;
    color: #fff;
    font-size: 2rem;
    margin: 0 100px; }

.logo {
  left: 120px;
  max-width: 50%;
  position: relative;
  top: 5px; }

ul.menu {
  background: transparent;
  margin-top: 20px;
  padding-right: 40px; }
  ul.menu a {
    font-size: 20px;
    margin: 0 20px; }
  ul.menu li {
    color: #fff; }

/* #4 - Forms */
label {
  font-size: 2rem; }

[type="text"], [type="password"], [type="date"], [type="datetime"], [type="datetime-local"], [type="month"], [type="week"], [type="email"], [type="number"], [type="search"], [type="tel"], [type="time"], [type="url"], [type="color"], textarea {
  -moz-border-bottom-colors: none;
  -moz-border-left-colors: none;
  -moz-border-right-colors: none;
  -moz-border-top-colors: none;
  border-color: -moz-use-text-color -moz-use-text-color #ccc;
  border-style: none none solid;
  border-width: 0 0 3px;
  box-shadow: none;
  font-size: 1.6rem;
  height: 3.5rem; }

[type="text"]:focus, [type="password"]:focus, [type="date"]:focus, [type="datetime"]:focus, [type="datetime-local"]:focus, [type="month"]:focus, [type="week"]:focus, [type="email"]:focus, [type="number"]:focus, [type="search"]:focus, [type="tel"]:focus, [type="time"]:focus, [type="url"]:focus, [type="color"]:focus, textarea:focus {
  -moz-border-bottom-colors: none;
  -moz-border-left-colors: none;
  -moz-border-right-colors: none;
  -moz-border-top-colors: none;
  border-color: -moz-use-text-color -moz-use-text-color #7BD7B2;
  border-style: none none solid;
  border-width: 0 0 3px;
  box-shadow: none; }

.close-button {
  color: #fff;
  font-size: 6rem; }

label {
  color: #fff; }

/* #5 - Mobile Menu */
#mobile-menu h2 {
  color: #fff; }

#mobile-menu a {
  text-align: center;
  color: #fff; }

#mobile-menu li {
  text-align: center; }

#mobile-menu ul.menu {
  padding: 0; }
